<template>
  <UserLayout class="bg-grey-lightest">
    <h1 slot="page-title" class="text-center my-8">Your Profile</h1>
    <div class="flex items-center justify-between border-b pb-4 mb-6">
      <h2 class="font-light text-lg">
        Change Password
      </h2>
    </div>
    <form @submit.prevent="submit">
      <div class="flex flex-col">
        <p class="w-1/2 my-1">
          Old Password
        </p>
        <div class="w-1/2 my-1 mb-4">
          <BasePassword
            v-model="password.old"
            v-validate="'required'"
            name="password"
            type="password"
            placeholder="Password"
          />
        </div>
        <p class="w-1/2 my-1">
          New Password
        </p>
        <div class="w-1/2 my-1">
          <BasePassword
            v-model="password.new"
            v-validate="'required'"
            name="password"
            type="password"
            placeholder="Password"
          />
        </div>
      </div>
      <div class="flex justify-end">
        <BaseButton
          class="rounded py-4 w-48 my-2 bg-gradient"
          type="submit"
        >
          <BaseIcon
            v-if="load"
            name="spinner"
            spin
          />
          <span
            v-else
            class="uppercase font-bold"
          >
            Save
          </span>
        </BaseButton>
      </div>
    </form>
  </UserLayout>
</template>

<script>
import UserLayout from '@layouts/User'
import { mapGetters, mapActions } from 'vuex'
import { signin } from '@utils/auth'
import { success } from '@utils/toast'

export default {
  page: {
    title: 'ChangePassword',
    meta: [{ name: 'description', content: 'ChangePassword' }],
  },

  components: { UserLayout },

  data () {
    return {
      password: {
        old: '',
        new: '',
      },
      load: false,
      message: '',
      validationErrors: {},
    }
  },

  computed: {
    ...mapGetters({
      user: 'auth/user',
    })
  },

  methods: {
    ...mapActions({
      changePassword: 'profile/changePassword',
    }),

    submit () {
      this.load = true

      this.changePassword({
        oldPassword: this.password.old,
        newPassword: this.password.new,
        cb: () => {
          success({
            text: 'Password successfully changed.',
            width: 500,
          })

          this.load = false
        }
      })
    },
  }
}
</script>

<style scoped>
/deep/ input {
  @apply bg-grey-lightest p-4 rounded my-2 border border-grey-lightest w-full;
}

/deep/ .border-red {
  @apply border-red
}
</style>
